<template>
  <popup ref="popup" @maskclick="$refs.popup.hide()">
    <div class="popup-wrap">
      <div class="popup-header">
        <p class="popup-header-date" @click="current = 'date'">{{pickerDate ? format(pickerDate) : '日期'}}</p>
        <p class="popup-header-time" @click="set">{{pickerTime ? formatTime(pickerTime) : '时间'}}</p>
        <p @click="confirm" class="popup-header-confirm">确定</p>
      </div>
      <div class="popup-date" v-show="current === 'date'">
        <table cellpadding="0" border="0" cellpacing="0">
          <thead>
              <tr>
                  <td v-for="week in weeks" class="week">{{week}}</td>
              </tr>
          </thead>
          <tbody>
          <tr v-for="v in daysUL">
            <td @click="picker(vv)" :class="dateClass(vv)" v-for="vv in v">{{vv.getDate()}}</td>
          </tr>
          </tbody>
        </table>
      </div>
      <div class="popup-time" v-show="current === 'time'">
        <picker
           @change="select"
           ref="picker"
           title="Choose Time"
           :data="data"
           :selectedIndex="selectedIndex"
          >

        </picker>
      </div>
    </div>
  </popup>
</template>
<script>
import Popup from '../popup/popup.vue'
import Picker from '../picker/picker.vue'
export default {
  data () {
    return {
      weeks:['日', '一', '二', '三', '四', '五', '六'],
      data: [['00','01','02','03','04','05','06','07'],['00','30']],
      selectedIndex: [0,0],
      currentYear: 1970,   // 年份
      currentMonth: 1,  // 月份
      currentDay: 1,    // 日期
      currentWeek: 1,    // 星期
      firstWeek:1,
      current: 'date',
      currentDate: new Date(),
      pickerDate: '',
      pickerTime: '',
      days:[],
      daysUL:[],
      min:[2010, 2, 1],
      max:[2020, 2, 1],
    }
  },
  created () {
    this.render();
  },
  methods: {
    confirm(){
      alert(this.format(this.pickerDate)+ ' ' +this.formatTime(this.pickerTime));
    },
    dateClass (d) {
      //如果在7天之内
      //抽离代码
      return {
        current: d.toString() === this.pickerDate.toString(),
        now: d.getDate() === this.currentDate.getDate() &&  d.getMonth() === this.currentDate.getMonth(),
        opacity: !(d.getDate() - this.currentDate.getDate() >= 0 &&  d.getDate() - this.currentDate.getDate() < 7 && d.getMonth() === this.currentDate.getMonth())
      };
    },
    select(i,a){
      this.$set(this.pickerTime,i , this.data[i][a])
    },
    //选中日期
    picker (d) {
      if(d.getDate() - this.currentDate.getDate() >= 0 &&  d.getDate() - this.currentDate.getDate() < 7 && d.getMonth() === this.currentDate.getMonth()){
        this.pickerDate = d;
      }
    },
    show () {
      this.$refs.popup.show();
    },
    set (){
      this.current = 'time'
      this.pickerTime = [this.data[0][0] , this.data[1][0]]
      this.$refs.picker.show()
    },
    format(date){
      return date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
    },
    formatTime(time){
      return time.join(':');
    },
    formatDate (year, month, day) {
        const y = year
        let m = month
        if (m < 10) m = `0${m}`
        let d = day
        if (d < 10) d = `0${d}`
        return `${y}-${m}-${d}`
    },
    render(){
      let date = new Date();
      this.currentDay = date.getDate()          // 今日日期 几号
      this.currentYear = date.getFullYear()       // 当前年份
      this.currentMonth = date.getMonth() + 1    // 当前月份
      this.currentWeek = date.getDay() // 1...6,0   // 今天是星期几
      //1号是星期几
      date.setDate(1);
      this.firstWeek = date.getDay() === 0 ? 7 : date.getDay();
      const str = this.formatDate(this.currentYear, this.currentMonth, 1)// 今日日期 年-月-日

      //获取之前的天数
      for (let i = this.firstWeek ; i >= 0; i -= 1) {
          const d = new Date(str)
          d.setDate(d.getDate() - i)
          this.days.push(d)
      }
      //处理1号是星期天为 7 的情况， 为7天就直接放在daysUL里
      if (this.days.length % 7 === 0){
          this.daysUL.push(this.days);
          this.days = [];
      }

      for (let i = 1; i <= 35 - this.firstWeek; i += 1) {
          const d = new Date(str);
          d.setDate(d.getDate() + i);
          this.days.push(d);
          if (this.days.length % 7 === 0){
              this.daysUL.push(this.days);
              this.days = [];   //清空重新存放天数
          }
      }
    },
  },
  components: {
    Picker,
    Popup
  }
}

</script>
<style lang="less" scoped>
.popup-wrap{
  width: 100%;
  height: auto;
  min-height: 300px;
  background: white;
}
.popup-header{
  display: flex;
  height: 45px;
  background: #eee;
  .popup-header-date{
    flex:1;
  }
  .popup-header-time{
    flex:1;
  }
  .popup-header-confirm{
    width: 80px;
    height: 100%;
    background: yellow;
  }
}

.popup-date table{
  width: 100%;
  text-align: center;
  td{
    padding:8px;
    border:0;
  }
  .opacity{
    opacity: 0.2;
  }
  .now{
    color: red;
  }
  .current{
    background: green;
  }
}

</style>



