<template>
  <div id="app">
    <img src="./assets/logo.png" @click="$refs.pc.show()">
    <picker-calendar ref="pc">

    </picker-calendar>
    <!--
    <transition name="cube-picker-move">
      <popup :center="false" type="my-popup" ref="myPopup">
          <div class="popup-wrap">
            <div class="popup-header">
              <p class="popup-header-date" @click="current = 'date'">日期</p>
              <p class="popup-header-time" @click="set">时间</p>
              <p class="popup-header-confirm">确定</p>
            </div>
            <div class="popup-date" v-show="current === 'date'">
              <table cellpadding="0" border="0" cellpacing="0">
                <thead>
                    <tr>
                        <td v-for="week in weeks" class="week">{{week}}</td>
                    </tr>
                </thead>
                <tbody>
                <tr v-for="v in daysUL" style="{'animation-delay',(k1*30)+'ms'}">
                  <td v-for="vv in v">{{vv.getDate()}}</td>
                </tr>
                </tbody>
              </table>
            </div>
            <div class="popup-time" v-show="current === 'time'">
              <picker
                 ref="picker"
                 title="Choose Time"
                 :data="data"
                 :selectedIndex="selectedIndex"
                >

              </picker>
            </div>
          </div>
      </popup>
    </transition>
    -->

    <router-view/>
  </div>
</template>

<script>
import Picker from './components/cascade-picker/cascade-picker.vue'
import PickerCalendar from './newcomponents/pickerCalendar/pickerCalendar.vue'
export default {
  name: 'app',
  mounted () {
    //必定是5行5列 35天
    //this.render(2017,10);
  },
  data () {
    return {
      pickerData:[[1,2,3],[3,4,5]],
      currentYear: 1970,   // 年份
      currentMonth: 1,  // 月份
      currentDay: 1,    // 日期
      currentWeek: 1,    // 星期
      firstWeek:1,
      current: 'date',
      days:[],
      daysUL:[],
      min:[2010, 2, 1],
      max:[2020, 2, 1],
      weeks:['日', '一', '二', '三', '四', '五', '六'],
      timeData: [range(0, 23, true, '点'), range(0, 59, true, '分'), range(0, 59, true, '秒')],
      selectedIndex:[0,0,0]
    }
  },
  computed: {
      data() {
        let data = range(this.min[0], this.max[0], false, '年')
        data.forEach(year => {
          let minMonth = year.value === this.min[0] ? this.min[1] : 1
          let maxMonth = year.value === this.max[0] ? this.max[1] : 12
          year.children = range(minMonth, maxMonth, false, '月')
          year.children.forEach(month => {
            let day = 30
            if ([1, 3, 5, 7, 8, 10, 12].includes(month.value)) {
              day = 31
            } else {
              if (month.value === 2) {
                day = !(year.value % 400) || (!(year.value % 4) && year.value % 100) ? 29 : 28
              }
            }
            let minDay = year.value === this.min[0] && month.value === this.min[1] ? this.min[2] : 1
            let maxDay = year.value === this.max[0] && month.value === this.max[1] ? this.max[2] : day
            month.children = range(minDay, maxDay, false, '日')
          })
        })
        console.log(data);
        return data
      },
  },
  methods: {
    set (){
      this.current = 'time'
      this.$refs.picker.show()
    },
    formatDate (year, month, day) {
        const y = year
        let m = month
        if (m < 10) m = `0${m}`
        let d = day
        if (d < 10) d = `0${d}`
        return `${y}-${m}-${d}`
    },
    render(){
      let date = new Date();
      this.currentDay = date.getDate()          // 今日日期 几号
      this.currentYear = date.getFullYear()       // 当前年份
      this.currentMonth = date.getMonth() + 1    // 当前月份
      this.currentWeek = date.getDay() // 1...6,0   // 今天是星期几
      date.setDate(1);
      this.firstWeek = date.getDay() === 0 ? 7 : date.getDay();
      const str = this.formatDate(this.currentYear, this.currentMonth, 1)// 今日日期 年-月-日
      this.days.length = 0

      // 今天是周日，放在第一行第7个位置，前面6个 这里默认显示一周，如果需要显示一个月，则第二个循环为 i<= 42- this.firstWeek
      for (let i = this.firstWeek - 1; i >= 0; i -= 1) {
          const d = new Date(str)
          d.setDate(d.getDate() - i)
          this.days.push(d)
      }
      //处理1号是星期天为 7 的情况， 为7天就直接放在daysUL里
      if (this.days.length % 7 === 0){
          this.daysUL.push(this.days);
          this.days = [];
      }

      for (let i = 1; i <= 42 - this.firstWeek; i += 1) {
          const d = new Date(str);
          d.setDate(d.getDate() + i);
          this.days.push(d);        //一个 days 就是一行7天  daysUL 就是个数组，里面有六个days  就是六行42天
          if (this.days.length % 7 === 0){
              this.daysUL.push(this.days);
              this.days = [];   //清空重新存放天数
          }
      }
      console.log(this.days,this.daysUL)


    },
    show () {
      this.$refs.myPopup.show()
    }
  },
  components: {
    PickerCalendar,
    Picker
  }
}
  function range(n, m, polyfill = false, unit = '') {
    let arr = []
    for (let i = n; i <= m; i++) {
      let value = (polyfill && i < 10 ? '0' + i : i) + unit
      arr.push({
        text: value,
        value: i
      })
    }
    return arr
  }
</script>

<style lang="less" scoped>
.popup-wrap{
  width: 100%;
  height: auto;
  min-height: 300px;
  background: white;
}
.popup-header{
  display: flex;
  height: 45px;
  background: #eee;
  .popup-header-date{
    flex:1;
  }
  .popup-header-time{
    flex:1;
  }
  .popup-header-confirm{
    width: 80px;
    height: 100%;
    background: yellow;
  }
}

.popup-date table{
  width: 100%;
  text-align: center;
  td{
    border:0;
  }
}
.cube-picker-move-enter, .cube-picker-move-leave-active{
  transform: translate3d(0, 273px, 0)
}

.cube-picker-move-enter-active, .cube-picker-move-leave-active{
  transition: all .3s ease-in-out
}
</style>



