<template>
  <transition name="fade">
    <div class="buzz-popup" v-show="isVisible">
      <div class="buzz-popup-mask" @click="maskClick"></div>
      <transition name="up">
        <div class="buzz-popup-content" v-show="isVisible">
          <slot></slot>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
  import apiMixin from '../../common/mixins/api'

  export default {
    name: 'buzz-pop',
    mixins: [apiMixin],
    methods: {
      maskClick(e) {
        this.$emit('maskclick', e)
      }
    }
  }
</script>

<style lang="less" scoped>
.buzz-popup{
  position: fixed;
  left: 0;
  right:0;
  top: 0;
  bottom: 0;
  z-index: 100;
  .buzz-popup-mask, .buzz-popup-container{
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .buzz-popup-mask{
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .buzz-popup-container{
    transform: translate(100%, 100%);
  }
  .buzz-popup-content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: white;
    min-height: 200px;
    transition: all .2s linear;
  }
  .fade-enter-active, .fade-leave-active {
    opacity:1;
  }
  .up-enter-active, .up-leave-active {
    transform: translate3d(0, 100%, 0);
  }
}

</style>



